<template>
  <div id="myinfo">
    <div class="button-group">
    <button class="button is-warning" @click="logout"></button>
    <button class="button is-danger" @click="unregister_user"></button>   
    </div>
  </div>
</template>

<script>
  import {
    mapGetters,
    mapActions
  } from 'vuex'
  export default {
    name: 'myinfo',
    data() {
      return {
        email: null
      } 
    },
    created() {

    },
    watch: {

    },
    computed: {
      ...mapGetters([
        'getInfo',
      ])
    },
    methods: {
      ...mapActions([
        'LOAOUT_AUTH',
        'UNREGISTER',
      ]),
      logout() {
        this.$dialog.confirm({
          title: '',
          message: ' ?',
          cancelText: '',
          confirmText: '',
          type: 'is-warning',
          hasIcon: true,
          onConfirm: () =>  {
          Kakao.Auth.logout()        
          this.LOAOUT_AUTH()
            .then((res) => {
              this.$cookie.delete('email')
              this.$cookie.delete('token')
              this.$router.push({
                path: '/sign'
              })
            })
          }
        })
      },
      unregister_user() {
        this.$dialog.confirm({
          title: '',
          message: ' ?',
          cancelText: '',
          confirmText: '',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () =>  {
            Kakao.Auth.logout()        
           this.UNREGISTER(this.getInfo.email)
            .then((res) => {
              this.$cookie.delete('email')
              this.$cookie.delete('token')
              this.$router.push({
                path: '/sign'
              })
            })
          }
        })
      }
    }
  }
</script>
<style src="../../assets/css/myinfo.scss" scoped>


