<template>
  <div id="sign">
    <div class="button-group">
      <button class="button is-warning" @click="auth_sign('kakao')"> </button>
      <button class="button is-info" @click="auth_sign('facebook')"> </button>
      <button class="button " @click="auth_sign('google')"> </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'sign',
  data () {
    return {
    }
  },
  created() {
    const sign = this.$cookie.get('sign')
    if(sign == "error") {
      this.$toast.open({
          duration: 3000,
          message: ` .   `,
          position: 'is-top',
          type: 'is-danger'
      })
    }
  },
  methods: {
    auth_sign(auth) {
      const baseURI = location.protocol + '//' + location.hostname + ':' + 3001 + '/api'
      console.log(baseURI)
      location.href=`${baseURI}/auth/${auth}`
    }
  }
}
</script>
<style src="../../assets/css/sign.scss" scoped>


